import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

import {
  BillPayPaymentsService,
  BillPayPayeesService,
  PayeeByIdGetResponseBody,
  BillPayPaymentsGetResponseBody,
  PaymentByIdGetResponseBody,
  RecurringPaymentByIdGetResponseBody,
} from '@backbase/data-ang/billpay';
import { PaymentsFilterStatus } from '../model';
import { PaymentType, ONE_OFF_PAYMENT, Direction } from '@backbase/billpay-journeys-common';

@Injectable()
export class ManageBillPaymentsDataService {
  /** @internal */
  constructor(
    private readonly paymentsData: BillPayPaymentsService,
    private readonly payeesData: BillPayPayeesService,
  ) {}

  getPayments(pageSize: number, paymentFilter: PaymentsFilterStatus): Observable<BillPayPaymentsGetResponseBody> {
    const getDirection = (status: PaymentsFilterStatus): Direction =>
      status === PaymentsFilterStatus.PENDING ? Direction.ASC : Direction.DESC;

    return this.paymentsData.getBillPayPayments({
      status: paymentFilter,
      direction: getDirection(paymentFilter),
      ...(pageSize && { size: pageSize }),
    });
  }

  getPayment(
    id: string,
    paymentType: PaymentType = ONE_OFF_PAYMENT,
  ): Observable<PaymentByIdGetResponseBody | RecurringPaymentByIdGetResponseBody> {
    return paymentType === ONE_OFF_PAYMENT
      ? this.paymentsData.getPaymentById({ id })
      : this.paymentsData.getRecurringPaymentById({ id });
  }

  getPayee(id: string): Observable<PayeeByIdGetResponseBody> {
    return this.payeesData.getPayeeById({ id });
  }

  deletePayment(id: string, paymentType: PaymentType = ONE_OFF_PAYMENT): Observable<HttpResponse<any>> {
    return paymentType === ONE_OFF_PAYMENT
      ? this.paymentsData.deletePaymentById({ id })
      : this.paymentsData.deleteRecurringPaymentById({ id });
  }
}
