import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { provideRoutes, Route, RouterModule } from '@angular/router';

import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import { BillpayEnrolmentGuard, BillpayJourneysCommonModule } from '@backbase/billpay-journeys-common';

import { ManageBillPaymentsNavigationService } from './services/manage-bill-payments-navigation.service';
import { ManageBillPaymentsDataService } from './services/manage-bill-payments-data.service';

import { BillPayPaymentsListViewComponent } from './views/billpay-payments-list-view/billpay-payments-list-view.component';
import { BillPayPaymentDetailsViewComponent } from './views/billpay-payment-details-view/billpay-payment-details-view.component';
import { BillPayEnrolmentViewComponent } from './views/billpay-enrolment-view/billpay-enrolment-view.component';
import { BillpayPaymentDetailsComponent } from './components/billpay-payment-details/billpay-payment-details.component';
import { BillpayPaymentStatusBadgeComponent } from './components/billpay-payment-status-badge/billpay-payment-status-badge.component';
import { BillpayPaymentsDeleteModalComponent } from './components/billpay-payments-delete-modal/billpay-payments-delete-modal.component';
import { ManageBillPaymentsJourneyComponent } from './manage-bill-payments-journey.component';
import { BillpayPaymentsGroupComponent } from './components/billpay-payments-group/billpay-payments-group.component';
import { BillpayPaymentCardComponent } from './components/billpay-payment-card/billpay-payment-card.component';

import { AlertModule } from '@backbase/ui-ang/alert';
import { BadgeModule } from '@backbase/ui-ang/badge';
import { BbDatePipeModule } from '@backbase/ui-ang/date-pipe';
import { IconModule } from '@backbase/ui-ang/icon';

const uiModules = [IconModule, AlertModule, BadgeModule, BbDatePipeModule];
const services = [ManageBillPaymentsNavigationService, ManageBillPaymentsDataService];
const components = [
  ManageBillPaymentsJourneyComponent,
  BillPayPaymentsListViewComponent,
  BillPayPaymentDetailsViewComponent,
  BillPayEnrolmentViewComponent,
  BillpayPaymentDetailsComponent,
  BillpayPaymentStatusBadgeComponent,
  BillpayPaymentsDeleteModalComponent,
  BillpayPaymentsGroupComponent,
  BillpayPaymentCardComponent,
];

export const defaultRoute: Route = {
  path: '',
  component: ManageBillPaymentsJourneyComponent,
  children: [
    { path: 'enrolment', component: BillPayEnrolmentViewComponent },
    {
      path: '',
      canActivate: [BillpayEnrolmentGuard],
      children: [
        { path: '', component: BillPayPaymentsListViewComponent, pathMatch: 'full' },
        { path: ':id', component: BillPayPaymentDetailsViewComponent },
      ],
    },
  ],
};

@NgModule({
  declarations: [...components],
  imports: [
    CommonModule,
    RouterModule,
    BillpayJourneysCommonModule,
    BackbaseCoreModule.withConfig({
      classMap: { ManageBillPaymentsJourneyComponent },
    }),
    ...uiModules,
  ],
  providers: [...services],
})
export class ManageBillPaymentsJourneyModule {
  static forRoot(
    data: { route: Route; [key: string]: any } = { route: defaultRoute },
  ): ModuleWithProviders<ManageBillPaymentsJourneyModule> {
    return {
      ngModule: ManageBillPaymentsJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
