import { Component, OnInit } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { ManageBillPaymentsDataService } from '../../services/manage-bill-payments-data.service';
import { ManageBillPaymentsJourneyConfigService } from '../../services/manage-bill-payments-journey-config.service';
import { Payment } from '@backbase/data-ang/billpay';
import { PaymentsGroup } from '../../model';

@Component({
  selector: 'bb-billpay-list-view',
  templateUrl: './billpay-payments-list-view.component.html',
})
export class BillPayPaymentsListViewComponent implements OnInit {
  payments$!: Observable<Payment[]>;
  hasLoadingError = false;
  /**
   * Array of payments details. Groups by date.
   */
  paymentsByDate: PaymentsGroup[] = [];

  /** @internal */
  constructor(
    private readonly dataService: ManageBillPaymentsDataService,
    readonly config: ManageBillPaymentsJourneyConfigService,
  ) {}

  ngOnInit(): void {
    this.payments$ = this.dataService.getPayments(this.config.pageSize, this.config.pageFilter).pipe(
      map(({ payments }) => payments),
      tap(paymentsData => {
        this.paymentsByDate = this.groupPaymentsByDate(paymentsData);
      }),
      catchError(() => {
        this.hasLoadingError = true;
        return of([]);
      }),
    );
  }

  private groupPaymentsByDate(payments: Payment[]): PaymentsGroup[] {
    const reducer = (groups: PaymentsGroup[], payment: Payment): PaymentsGroup[] => {
      const lastGroup = groups[groups.length - 1];
      const { paymentDate } = payment;
      if (lastGroup && this.isSameDay(lastGroup.date, paymentDate)) {
        lastGroup.payments.push(payment);
      } else {
        groups.push({
          date: paymentDate,
          payments: [payment],
        });
      }
      return groups;
    };

    return payments.reduce(reducer, []);
  }

  private isSameDay(date1: string, date2: string): boolean {
    const day1 = new Date(date1).setHours(0, 0, 0, 0);
    const day2 = new Date(date2).setHours(0, 0, 0, 0);
    return day1 === day2;
  }
}
