import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { Payment, Payee } from '@backbase/data-ang/billpay';
import { BillpayPaymentParserService, PaymentReview } from '@backbase/billpay-journeys-common';
import { PaymentStatus, PaymentEditEvent } from '../../model';

@Component({
  selector: 'bb-billpay-payment-details',
  templateUrl: './billpay-payment-details.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentDetailsComponent {
  /**
   * Details of the payment.
   */
  @Input() payment?: Payment;
  /**
   * Details of the payee that is the subject of the payment.
   */
  @Input() payee?: Payee;
  /**
   * Whether the account number should be masked. Defaults to true.
   */
  @Input() accountNumberMasked = true;
  @Output() paymentCancelling = new EventEmitter<void>();
  @Output() paymentEditing = new EventEmitter<PaymentEditEvent>();
  readonly paymentStatus = PaymentStatus;

  /** @internal */
  constructor(private readonly paymentParserService: BillpayPaymentParserService) {}

  parseToReview(payment: Payment, payee: Payee): PaymentReview {
    return this.paymentParserService.paymentAndPayeeResponsesToReview(payment, payee);
  }

  cancelPayment() {
    this.paymentCancelling.emit();
  }

  editPayment(payment: Payment) {
    this.paymentEditing.next({
      id: payment.id,
      recurring: payment.recurring,
    });
  }
}
