import { Component, OnDestroy, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { combineLatest, Observable, of, Subject, throwError } from 'rxjs';
import { catchError, map, shareReplay, switchMap, takeUntil, tap } from 'rxjs/operators';

import { NotificationService } from '@backbase/ui-ang/notification';
import { OneOffPayment, Payee, Payment } from '@backbase/data-ang/billpay';
import { ManageBillPaymentsNavigationService } from '../../services/manage-bill-payments-navigation.service';
import { ManageBillPaymentsJourneyConfigService } from '../../services/manage-bill-payments-journey-config.service';
import { ManageBillPaymentsDataService } from '../../services/manage-bill-payments-data.service';
import { ONE_OFF_PAYMENT, PaymentType } from '@backbase/billpay-journeys-common';
import { PaymentEditEvent } from '../../model';

@Component({
  selector: 'bb-billpay-payment-details-view',
  templateUrl: './billpay-payment-details-view.component.html',
})
export class BillPayPaymentDetailsViewComponent implements OnDestroy {
  @ViewChild('notificationHeader') notificationHeaderTemplate: TemplateRef<any> | string = '';
  @ViewChild('notificationMessage') notificationMessageTemplate: TemplateRef<any> | string = '';
  payment$: Observable<Payment | OneOffPayment | undefined>;
  payee$: Observable<Payee | undefined>;
  hasLoadingError = false;
  isDeleteModalOpened = false;
  id$ = this.route.paramMap.pipe(map(params => params.get('id') || ''));
  private readonly paymentType$ = this.route.queryParamMap.pipe(
    map(params => params.get('type') || ONE_OFF_PAYMENT),
  ) as Observable<PaymentType>;
  private readonly destroy$ = new Subject<void>();

  constructor(
    readonly config: ManageBillPaymentsJourneyConfigService,
    private readonly dataService: ManageBillPaymentsDataService,
    private readonly route: ActivatedRoute,
    private readonly routerService: ManageBillPaymentsNavigationService,
    private readonly notificationService: NotificationService,
  ) {
    this.payment$ = combineLatest([this.id$, this.paymentType$]).pipe(
      takeUntil(this.destroy$),
      switchMap(([id, paymentType]) => this.dataService.getPayment(id, paymentType)),
      map(({ payment }) => payment),
      shareReplay(1),
      catchError(() => {
        this.hasLoadingError = true;
        return of(undefined);
      }),
    );

    this.payee$ = this.payment$.pipe(
      switchMap(payment =>
        payment && !payment.payeeElectronic && payment.payeeID
          ? this.dataService.getPayee(payment.payeeID)
          : throwError("Payee can't be retrieved"),
      ),
      map(({ payee }) => payee),
      catchError(() => {
        return of({} as Payee);
      }),
    );
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onPaymentDeleteFlowStarted() {
    this.isDeleteModalOpened = true;
  }

  onEditPayment(payment: PaymentEditEvent) {
    if (payment.recurring) {
      this.routerService.navigateToEditRecurringPayment(payment.id);
    } else {
      this.routerService.navigateToEditOneOffPayment(payment.id);
    }
  }

  onModalResolve(resolvedWith: boolean) {
    const deletePayment$ = this.deletePayment().pipe(
      tap(() => {
        this.routerService.navigateToPaymentsList(this.config.pageFilter);
        this.notificationService.showNotification({
          header: this.notificationHeaderTemplate,
          message: this.notificationMessageTemplate,
          modifier: 'success',
          ttl: this.dismissTimeout,
        });
      }),
    );
    (resolvedWith ? deletePayment$ : of()).subscribe({
      complete: () => (this.isDeleteModalOpened = false),
    });
  }

  private deletePayment() {
    return combineLatest([this.id$, this.paymentType$]).pipe(
      takeUntil(this.destroy$),
      switchMap(([id, paymentType]) => this.dataService.deletePayment(id, paymentType)),
    );
  }

  private get dismissTimeout(): number {
    if (this.config.notificationDismissTime === undefined) {
      throw new Error('Dismiss timeout value not found');
    }
    return this.config.notificationDismissTime;
  }
}
