import { Injectable, Optional, Inject, InjectionToken } from '@angular/core';
import '@angular/localize/init';

export const ManageBillPaymentsJourneyConfigurationToken = new InjectionToken<ManageBillPaymentsJourneyConfiguration>(
  'ManageBillPaymentsJourneyJourneyConfiguration injection token',
);

export interface ManageBillPaymentsJourneyConfiguration {
  notificationDismissTime: number;
  pageSize: number;
  pageFilter: PaymentsFilterStatus;
  accountNumberMasked: boolean;
  pageTitle: string;
}

export enum PaymentsFilterStatus {
  PENDING = 'PENDING',
  HISTORICAL = 'HISTORICAL',
  ALL = 'ALL',
}

const configDefaults: ManageBillPaymentsJourneyConfiguration = {
  notificationDismissTime: 5000,
  pageSize: 0,
  pageFilter: PaymentsFilterStatus.PENDING,
  accountNumberMasked: false,
  pageTitle: $localize`:@@manage-bill-payments.page-title:Pending payments`,
};

@Injectable()
export class ManageBillPaymentsJourneyConfigService {
  private _config: ManageBillPaymentsJourneyConfiguration;

  constructor(
    @Optional() @Inject(ManageBillPaymentsJourneyConfigurationToken) config: ManageBillPaymentsJourneyConfiguration,
  ) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
  }

  get defaults(): ManageBillPaymentsJourneyConfiguration {
    return configDefaults;
  }

  get notificationDismissTime(): number {
    return this._config.notificationDismissTime;
  }

  get pageSize(): number {
    return this._config.pageSize;
  }

  get pageFilter(): PaymentsFilterStatus {
    return this._config.pageFilter;
  }

  get accountNumberMasked(): boolean {
    return this._config.accountNumberMasked;
  }

  get pageTitle(): string {
    return this._config.pageTitle;
  }
}
