import { Component, Input } from '@angular/core';
import { ManageBillPaymentsNavigationService } from '../../services/manage-bill-payments-navigation.service';
import { PaymentsGroup } from '../../model';

export enum DateLabel {
  yesterday = 'yesterday',
  today = 'today',
  tomorrow = 'tomorrow',
}

@Component({
  selector: 'bb-billpay-payments-group',
  templateUrl: './billpay-payments-group.component.html',
})
export class BillpayPaymentsGroupComponent {
  /**
   * Details of the payments within the group.
   */
  @Input() paymentsGroup?: PaymentsGroup;

  /** @internal */
  constructor(private readonly router: ManageBillPaymentsNavigationService) {}

  getDateLabel(date: string): DateLabel | undefined {
    const day = 1000 * 60 * 60 * 24;
    const today = new Date().setHours(0, 0, 0, 0);
    const targetDay = new Date(date).setHours(0, 0, 0, 0);
    const daysDiff = Math.round((targetDay - today) / day);
    switch (daysDiff) {
      case -1:
        return DateLabel.yesterday;
      case 0:
        return DateLabel.today;
      case 1:
        return DateLabel.tomorrow;
      default:
        return;
    }
  }

  navigateToPayment(id: string, isRecurring: boolean) {
    this.router.navigateToPayment(id, isRecurring);
  }
}
